import { Component, Prop, Watch, h } from '@stencil/core';
import { Chart, LineController, LineElement, PointElement, CategoryScale, LinearScale, Title,LogarithmicScale} from 'chart.js';
import { Item } from '../../types/Item';

Chart.register(LineController, LineElement, PointElement, CategoryScale, LinearScale,LogarithmicScale, Title);

@Component({
  tag: 'item-lookup-component',
  styleUrl: 'item-lookup-component.css',
  shadow: false,
})
export class ItemLookupComponent {
  canvasEl!: HTMLCanvasElement;
  chart: Chart;
  @Prop() item:Item;
  @Watch('item')
  onItemChange(newValue: Item) {
    this.updateChart(newValue); // Mettre  jour le graphique avec les nouvelles donnes
  }
  componentWillLoad() {
    this.createChart(this.item);
  }
  createChart(item: Item) {
    const ctx = this.canvasEl.getContext('2d');
    if (!ctx) {
      console.error('Canvas context introuvable.');
      return;
    }

    const labels = item.averagePrices.map((price) => price.createdAt);
    const averagePrices = item.averagePrices.map((price) => price.averagePrice);

    const minValue = Math.min(...averagePrices) * 0.1; // 10% en dessous du minimum
    const maxValue = Math.max(...averagePrices) * 1.9; // 10% au-dessus du maximum

    // Crer le graphique
    this.chart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: labels, // Labels pour l'axe X
        datasets: [
          {
            label: 'Prix moyen au fil du temps',
            data: averagePrices, // Donnes pour l'axe Y
            borderColor: 'rgb(75, 192, 192)',
            fill: false,
            tension: 0.1, // Lissage de la ligne
          },
        ],
      },
      options: {
        responsive: true,
        plugins: {
          title: {
            display: true,
            text: 'volution des prix moyens',
          },
        },
        scales: {
          x: {
            type: 'category', // chelle de catgories pour l'axe X
            title: {
              display: true,
              text: 'Date',
            },
          },
          y: {
            type: 'linear', // chelle linaire pour l'axe Y
            title: {
              display: true,
              text: 'Prix',
            },
            min: minValue, // Valeur minimale calcule
            max: maxValue, // Valeur maximale calcule
          },
        },
      },
    });
  }

  updateChart(item: Item) {
    if (!this.chart) {
      this.createChart(item); // Si le graphique n'existe pas, le crer
      return;
    }

    // Mettre  jour les donnes du graphique
    this.chart.data.labels = item.averagePrices.map((price) => price.createdAt);
    this.chart.data.datasets[0].data = item.averagePrices.map((price) => price.averagePrice);

    // Mettre  jour les limites de l'axe Y
    const averagePrices = item.averagePrices.map((price) => price.averagePrice);
    this.chart.options.scales.y.min = Math.min(...averagePrices) * 0.1;
    this.chart.options.scales.y.max = Math.max(...averagePrices) * 1.9;

    // Rafrachir le graphique
    this.chart.update();
  }
  render() {
    return (

        <div class="row">
          <div class="col-6">{this.item.name}</div>
          <div class="col-6"><canvas ref={el => (this.canvasEl = el as HTMLCanvasElement)} /></div>
          
        </div>
    );
  }
}
